import * as React from "react"

function MaxIcon1(props) {
  return (
    <svg
      {...props}
      width="1em"
      height="1em"
      viewBox="0 0 400 400"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <circle cx={200} cy={200} r={200} fill="#79E6F3" />
    </svg>
  )
}

export default MaxIcon1
