import * as React from "react"

function MinIcon1(props) {
  return (
    <svg
      {...props}
      width="1em"
      height="1em"
      viewBox="0 0 400 100"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <rect width={400} height={100} rx={45} fill="#FFF500" />
    </svg>
  )
}

export default MinIcon1
